import React from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom";
import { Result } from "./result/Result";
import { Create } from "./create/Create";

import netf from "./header/netf.svg";
import leech from "./header/leech.svg";
import "./App.scss";

function App() {
  return (
    <Router>
      <header>

        <div className="header-logo center">
          <img src={netf} className="netf" alt="netf" />
          <img src={leech} className="leech" alt="leech" />

          <p className="header-text">
            Free streaming budget calculator
          </p>
        </div>

      </header>
      <div className="body">
        <NavLink exact to="/" className="m-1">
          Home
        </NavLink>
        <NavLink exact to="/create" className="m-1">
          Create
        </NavLink>

        <span className="m-2">Tests:</span>

        <NavLink exact to="/a3f" className="m-1">
          /a3f
        </NavLink>
        <NavLink exact to="/b52" className="m-1">
          /b52
        </NavLink>
        <NavLink exact to="/aaZb52" className="m-1">
          /aaZb52
        </NavLink>
        <Switch>
          <Route path="/create" component={() => <Create />} />

          <Route
            path="/:result([\w+-]{3,})"
            component={(props: any) => (
              <Result result={props.match?.params?.result as string} />
            )}
          />

          <Route path="/">
            <div className="App">

              {/* main content */}
              <div className="app-description row d-flex justify-content-center">
                <p className="col-md-4">
                  Ever wondered how much you <span className="highlight">spent</span> on streaming?
                </p>

                <div className="w-100"></div>

                <p className="col-md-4">
                  Calculate for free your Netflix, Spotify, HBO Go or other streaming service budgets with Netfleech.
                </p>
              </div>

            </div>
          </Route>
        </Switch>
      </div>
      <footer>
        <p className="center">build: "{ process.env.REACT_APP_COMMIT_SHA }"</p>

        <p>
          <span>Photo by <a href="https://unsplash.com/@wesleyphotography?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">Wesley Tingey</a> on <a href="https://unsplash.com/?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">Unsplash</a></span>
        </p>
      </footer>
    </Router>
  );
}

export default App;
