import React, { useState, useEffect } from "react";
import { Form, FormGroup, FormLabel } from "react-bootstrap";
import Calendar from "react-calendar";

import Base64encoder from "../utils/Base64";
import * as Services from "../services/Services";
import { Result } from "../result/Result";

import "react-calendar/dist/Calendar.css";

const Base64 = new Base64encoder();

export const CreateItem = () => {
  const [type, setType] = useState<any>(0);
  const [fromDate, setFromDate] = useState<Date>(initializeDate());
  const [url, setUrl] = useState<any>();

  function handleMonthChange(date: Date | Date[]) {
    console.log("month changed", { date });

    if (Array.isArray(date)) {
      if (date && date.length > 0) {
        setFromDate(date[0]);
      }
    } else {
      setFromDate(date);
    }
  }

  function handleTypeChange(e: React.ChangeEvent<HTMLInputElement>) {
    console.log("type changed", e.currentTarget.value);

    setType(e.currentTarget.value);
  }

  useEffect(() => {
    function generateUrl() {
      console.log("generateUrl()", { type, fromDate });

      if (typeof type !== "undefined" && fromDate) {
        const serviceType = Base64.fromInt(type);
        const monthsElapsed = Base64.fromInt(
          calculateMonthsElapsed(fromDate),
          2
        );

        return `${serviceType}${monthsElapsed}`;
      } else {
        return ``;
      }
    }

    setUrl(generateUrl());
  }, [type, fromDate]);

  function renderResult() {
    if (url && url.length >= 3) return <Result result={url} />;
  }

  return (
    <div className="row">
      <Form className="create-form col-md-4 offset-md-1" noValidate>
        <FormGroup className="row">
          <FormLabel className="col-sm-4">Type</FormLabel>
          <div className="col-sm-8">
            <Form.Control as="select" custom onChange={handleTypeChange}>
              {Services.services.map((service, index) => (
                <option key={index} value={index}>
                  {service.name}
                </option>
              ))}
            </Form.Control>
          </div>
        </FormGroup>

        <FormGroup className="row">
          <FormLabel className="col-sm-4">From</FormLabel>
          <div className="col-sm-8">
            <Calendar
              view="year"
              value={fromDate}
              onClickMonth={handleMonthChange}
              maxDate={new Date()}
              showNeighboringMonth
            />
          </div>
        </FormGroup>

        <FormGroup className="row">
          <FormLabel className="col-sm-4">Url value</FormLabel>

          <pre className="col-sm-8">{url}</pre>
        </FormGroup>
      </Form>

      <div className="col-md-4 offset-md-1">{url && renderResult()}</div>
    </div>
  );
};

function initializeDate(): Date {
  const date = new Date();
  date.setDate(0);
  date.setHours(0, 0, 0, 0);
  date.setMonth(date.getMonth() - 6);
  return date;
}

function calculateMonthsElapsed(dateFromStr: string | Date) {
  if (!dateFromStr) return 0;
  const dateFrom = new Date(dateFromStr);

  const now = new Date();
  return (
    now.getMonth() -
    dateFrom.getMonth() +
    12 * (now.getFullYear() - dateFrom.getFullYear())
  );
}
