import React, { useState, useEffect } from "react";
import Base64encoder from "../utils/Base64";
import * as Services from "../services/Services";

const RESULT_ITEM_LENGTH = 3;
const Base64 = new Base64encoder();

export const Result = ({ result }: { result: string }) => {
  const [items, setItems] = useState<any[]>();

  useEffect(() => {
    try {
      setItems(handleResultParams(result));
    } catch (error) {
      console.log("Result error", error);
    }
  }, [result]);

  return (
    <div className="App row p-3">
      <div className="col App-header">
        <p>Result:</p>

        {items &&
          items.map((item, index) => {
            return (
              <div key={item.encoded}>
                <pre
                  style={{
                    textAlign: "left",
                    backgroundColor: "gray",
                    borderRadius: "25px",
                    padding: "10px"
                  }}
                >
                  {JSON.stringify(item, null, 2)}
                </pre>
              </div>
            );
          })}
      </div>
    </div>
  );
};
function decodeResultItem(itemStr: string) {
  const serviceTypeX64 = itemStr.charAt(0);
  const monthsElapsedX64 = itemStr.substr(1, RESULT_ITEM_LENGTH - 1);

  const serviceTypeId = Base64.toInt(serviceTypeX64);
  const monthsElapsed = Base64.toInt(monthsElapsedX64);

  console.log("Decode params:", {
    serviceTypeX64,
    serviceTypeId,
    monthsElapsedX64,
    monthsElapsed
  });

  const serviceType = Services.findService(serviceTypeId);

  return {
    encoded: itemStr,
    serviceTypeId,
    serviceType,
    monthsElapsed,
    price: monthsElapsed * serviceType.pricePerPersonInEur,
    startDate: calculateStartDate(monthsElapsed)
  };
}

function handleResultParams(result: string) {
  if (!result) {
    throw new Error("result params is empty!");
  }
  if (result.length % RESULT_ITEM_LENGTH !== 0) {
    throw new Error(
      `result param length is invalid - not multiple of ${RESULT_ITEM_LENGTH}`
    );
  }

  const length = result.length;
  const items = length / RESULT_ITEM_LENGTH;
  const parsedItems = [];

  for (let i = 0; i < items; i++) {
    const startIndex = i * RESULT_ITEM_LENGTH;
    const itemStr = result.substr(startIndex, RESULT_ITEM_LENGTH);

    try {
      const item = decodeResultItem(itemStr);

      parsedItems.push(item);
    } catch (error) {
      console.log(`Couldn't parse item: ${itemStr}`, error);
    }
  }

  console.log("parsedItems", parsedItems);

  return parsedItems;
}

function calculateStartDate(monthsElapsed: number) {
  const date = new Date();
  date.setMonth(-monthsElapsed);
  date.setHours(0, 0, 0, 0);
  return date;
}
