interface ServiceType {
  name: string;
  startDate: Date;
  pricePerPersonInEur: number;
}

export class Netflix implements ServiceType {
  name = "Netflix";
  startDate = new Date("2013-08-01");
  pricePerPersonInEur = 13.99 / 5;
}

export class Spotify implements ServiceType {
  name = "Spotify";
  startDate = new Date("2014-10-01");
  pricePerPersonInEur = 7.99 / 6;
}

export const services: ServiceType[] = [new Netflix(), new Spotify()];

export function findService(index: number) {
  const service = services[index];
  if (!service) {
    throw new Error(`ServiceType not found by ID: ${index}`);
  }
  return service;
}
