export default class Base64encoder {
  constructor() {
    const digitsStr =
      // 2     8       16      24      32      40      48      56     63
      // v     v       v       v       v       v       v       v      v
      "abcdefghijklmnopqrstuvwxyz0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ+-";
    this.digits = digitsStr.split("");
    this.digitsMap = new Map<string, number>();
    for (var i = 0; i < this.digits.length; i++) {
      this.digitsMap.set(this.digits[i], i);
    }
  }

  private digits: string[];
  private digitsMap: Map<string, number>;

  public fromInt(int32: number, fixedLength?: number) {
    let result = "";
    while (true) {
      result = this.digits[int32 & 0x3f] + result;
      int32 >>>= 6;
      if (int32 === 0) break;
    }
    if (fixedLength) {
      result = result.padStart(fixedLength, this.digits[0]);
    }
    return result;
  }

  public toInt(digitsStr: string) {
    let result = 0;
    for (var i = 0; i < digitsStr.length; i++) {
      result = (result << 6) + (this.digitsMap.get(digitsStr[i]) || 0);
    }
    return result;
  }
}
